import axios from 'axios';
import netWrokUtils from './NetWrokUtils'

//json
function formatPara(info) {
  let str = '';
  for (let i in info) {
    str += i + '=' + info[i] + '&';
  }
  str = str.substr(0, str.length - 1);
  return str;
}

