class Util {
  // static checkPatientStatus(auth) {
  //   return 'patientLogin';
  // }
  ;
  static getParameterByName(name, url) {
    if (!url) {
      url = window.location
    }
    var match = RegExp('[?&]' + name + '=([^&]*)').exec(url.search);
    return match && decodeURIComponent(match[1].replace(/\+/g, ' '));
  }

  // 
  //   mounted netWrokUtils.Util.goBackTo(function(that){that.$router.push('docLogin')}, this);
  static goBackTo(callback, currentObj) {
    var state = {
      url: ""
    };
    window.history.pushState(state, state.title, state.url);
    window.addEventListener("popstate", function (e) {  //
      console.log("");
      if (callback)
        callback(currentObj);
      // location.href = url;  //
    }, false);
  }

}

class ApiHost {
  static NEO = process.env.API_HOST;
  static MALL = process.env.API_HOST_MALL;
}

