<script>
  import axios from 'axios';
  import {MessageBox,Toast, Indicator} from 'mint-ui';
  import netWrokUtils from '../../components/NetWrokUtils'

  var timer = null;

  export default {
    beforeRouteEnter(to, from, next){

      var userid = localStorage.getItem(appUserId);
      var params = {
        authentication: auth,
        user_id: userid ? userid :''
      };

      function getParameterByName(name) {
        var match = RegExp('[?&]' + name + '=([^&]*)').exec(window.location.search);
        return match && decodeURIComponent(match[1].replace(/\+/g, ' '));
      }
      netWrokUtils.post('/wechat/' + gzhId + '/doctorStatus', params, (result) => {
        //status  123 4
        let status = result.data.data.status;
        if(status == 3 || status == 4){
          if(status == 4){
            //
            if(getParameterByName("is_login")){
              next(true);
            }else{
              localStorage.setItem(appUserId, "");
              localStorage.setItem(authKey, "");

              next({name: 'docLogin'})
            }
          }
          next(true);
        }else if(status == 1){
//          next({name: 'patInfoPerfect', query: {'from': to.path}}) //
        }else{
          Toast('');
          //andriod
          var parser = new UAParser();
          let message = parser.getResult();
          if(message.os.name == "Android"){
            setTimeout(()=>{
              wx.closeWindow();
            }, 1500);
          }else{
            wx.closeWindow();
          }
        }
      }, (error_result) => {
        Toast(error_result.data.msg);
      })
    },
    data() {
      return {
        getCode: "",
        time: 60,
        isCode: false,
        active: false,
        code: '',
        authentication: auth,
        flag_testReportIdentityCard: false,
        requestJson: {
          phone: '',
          code: '',
          tempPhone: ''
        },
        isShowClose: false,
        isShowClearPhone: false,
        phonetype:'',
        loginIsOn:true,
        isShowClearPhone:false, //
        islayershow:false, //

      }
    },
    created() {
      //bodybg
      document.getElementsByTagName("body")[0].style.background = "#f4f4f4";
      var parser = new UAParser();
      this.phonetype = parser.getResult().os.name;

    },
    mounted(){
      document.getElementsByTagName('title')[0].innerHTML = '';
      var that = this;
      setTimeout(function(){
        that.islayershow = true;
      }, 700);
    },
    watch: {
      'requestJson.phone'(){
        if (!(/^1[34578]\d{9}$/.test(this.requestJson.phone))) {
          this.isCode = false;
          this.isShowClearPhone = true;
          this.loginIsOn = true;
        }else{
          if(this.requestJson.code.length == 4){
            this.loginIsOn = false;
          }else{
            this.loginIsOn = true;
          }
          this.isShowClearPhone = true;
          this.time = 60;
          this.isCode = true;
          clearInterval(timer);       //
          document.getElementById("getCode").innerHTML = '';
        }
        if(!this.requestJson.phone){
          this.isShowClearPhone = false;
        }
      },
      'requestJson.code'() {
        if (this.requestJson.code.length == 4 && this.requestJson.phone){
          this.loginIsOn = false;
        } else {
          this.loginIsOn = true;
        }
      },
    },
    methods: {
      getMsgCode(el) {
        if(!this.isCode){
          return false;
        }
        if (!(/^1[34578]\d{9}$/.test(this.requestJson.phone))) {
          Toast('');
          return false
        } else {

          let _that = this;
          if (this.time >= 0 && !this.isCode) {

            return false
          } else {

            var params = {
              mobile: this.requestJson.phone
            }

            // 
            netWrokUtils.post('/wechat/'+ gzhId +'/sendCode', params, (result) => {
//              this.requestJson.code = result.data.data.code;
              //

              this.isCode = false;
              //
              clearInterval(timer);
              timer = setInterval(function () {
                if (_that.time >= 0) {
                  el.target.innerHTML = _that.time + 's'
                  _that.time--;

                }
                if (_that.time === -1) {
                  _that.time = 60;
                  _that.isCode = true;
                  clearInterval(timer);       //
                  el.target.innerHTML = ''
                }
              }, 1000)

              Indicator.close();
            }, (error_result) => {


              Indicator.close();
              console.log(error_result);
              Toast(error_result.data.msg);
            })
          }
        }
      },
      clearPhone(){
        this.requestJson.phone = ''
      },
      toLogin(){
        if(this.loginIsOn){
          return false;
        }else{
          this.loginIsOn = false;
        }


        let _this = this;
        if (_this.requestJson.phone == '' || _this.requestJson.phone.length != 11) {
          Toast('');
          return false
        } else if (!this.checkPhone(_this.requestJson.phone)) {
          Toast('');
          return false
        }
        if (_this.requestJson.code == '' || _this.requestJson.code.length != 4) {
          Toast('');
          return false
        }
        Indicator.open('');

        var params = {
          authentication: auth,
          mobile: _this.requestJson.phone,
          type: _this.phonetype,
          code:_this.requestJson.code
        }

        netWrokUtils.post('/wechat/' + gzhId + '/login', params, (result) => {

          //
          if(result.data.code == 100004){
            MessageBox.confirm('').then(action => {
              netWrokUtils.post('/wechat/' + gzhId + '/replaceDevice', params, (result) => {
                Indicator.open('');
                localStorage.setItem(appUserId, result.data.data.user_id);
                //status   1
                wx.closeWindow(); //

              }, (error_result) => {
                Toast(error_result.data.msg);
              })
            },action => {
              Toast(error_result.data.msg);
            });
            return false;

//            
          }else{
            Indicator.open({text:'',spinnerType:'snake'});
            localStorage.setItem(appUserId, result.data.data.user_id);
            Indicator.close();
            wx.closeWindow();
          }
          this.loginIsOn = false;
        }, (error_result) => {
          this.loginIsOn = false;
          Indicator.close();
          Toast(error_result.data.msg);
        })
      },
      checkPhone(phone) {
        if (!(/^1[34578]\d{9}$/.test(phone))) {
          return false;
        } else {
          return true;
        }
      },
      goUserSignDeal(){
        this.$router.push({name: 'userSignDeal'})
      },
      readAticle(){
        this.islayershow = false;
        document.getElementById("agreeRead").checked = true;
      },
      agreeRead(){
        event.preventDefault();
        return false;
      },
      patArgeeBook(){
        this.islayershow = true;
      }
    }
  }

</script>




